.tikitaka-editor, .tikitaka {
  position: relative;
  width: 740px;
  margin: 0 auto; }

.tikitaka__title {
  border: none;
  margin-bottom: 3rem;
  font-size: 2.5rem;
  font-weight: 500; }

.image-container {
  margin-top: 2rem;
  text-align: center; }
  .image-container img {
    display: block;
    width: 100%;
    margin: 0 auto; }

.iframe-container {
  position: relative;
  width: 100%;
  height: 100%; }
  .iframe-container iframe {
    position: absolute;
    width: 100%;
    height: 100%;
    border: none; }



